@import './src/varsAndMixins.scss';
/* heder -------------------------------------- */
.non-product {
  max-width: 1280px;
  margin: 0 auto;
  padding-top: 100px;
  background: #f1edea;
  padding-bottom: 30px;
}
.non-product__links {
  @include flex-center-center;
  a {
    text-decoration: none;
    color: green;
  }
  p {
    width: 100px;
    color: black;
  }
  span {
    margin: 0 20px;
    color: black;
  }
}
.non-product__title {
  @include flex-center-center;
  text-align: center;
  h4  {
    margin: 30px 0 0 0;
    padding: 0;
    font-size: 60px;
  }
  hr {
    width: 45%;
    padding: 0;
    margin: 30px 50px 0 50px;
    color: #D3D3D3;
  }
}
// content  ---------------------------------
.non-product__content {
  @include flex-center-center;
  height: 500px;
  width: calc(100% - 100px);
  background: white;
  margin: 30px auto;
}
// left bar ---------------------------------
.non-product__left-bar {
  @include flex-center-center;
  width: 40%;
  background-image: url('/components/non_product/oops.png');
  background-repeat: no-repeat;
  background-position: left;
  background-size: 300px 300px;
  background-position-x: -30px;
  min-width: 370px;
  h4 {
    color: #84bc22;
    font-size: 120px;
    font-weight: bold;
  }
}

// right bar --------------------------------
.non-product__right-bar {
  width: 60%;
  min-width: 370px;
  a {
    text-decoration: none;
    color: #8cc031;
    font-size: 20px;
    display: block;
    margin: 25px 0 30px 0;
  }
  h3 {
    margin: 0;
    padding: 0;
    font-size: 30px;
  }
  p {
    margin: 30px 0;
    color: #555555;
  }
}
.non-product__search-wrapper {
  position: relative;
}
.non-product__search {
  margin-top: 20px;
  border: 1px solid #e8e2d6;
  height: 25px;
  width: 75%;
  border-radius: 20px;
  padding: 0 20px;
}
.non-product__search:focus {
  border: 1px solid #8cc031;
}
.non-product__search-btn {
  cursor: pointer;
  border: none;
  background: none;
  position: absolute;
  left: 75%;
  top: 40%;
  img {
    height: 20px;
    width: 20px;
  }
}

@media(max-width: 850px) {
  .non-product__left-bar { 
    background-position-x: -80px;
  }
  .non-product__content {
    flex-wrap: wrap;
    height: 100%;
    padding: 0 0 30px 0; 
  }
}
@media(max-width: 500px) {
  .non-product__left-bar,.non-product__right-bar {
    min-width: 250px;
  }
  .non-product__right-bar a {
    font-size: 12px;
  }
  .non-product__left-bar { 
    background-size: contain;
    h4 {
      font-size: 70px;
    }
  }
}
@media(max-width: 350px) {
  .non-product__left-bar,.non-product__right-bar {
    min-width: 200px;
  }
  .non-product__right-bar {
    h3 {
      font-size: 20px;
    }
  }
  .non-product__title {
    hr {
      width: 45%;
      margin: 30px 20px 0 20px;
    }
  }
}