// tabs --------------------------------------------------
.product__tabs-wrapper {
  padding: 0 50px;
  min-height: 300px;
}
.MuiButtonBase-root.MuiTab-root.MuiTab-textColorInherit.Mui-selected {
  background: #262a33;
  color: white;
  border: 2px solid #e8e2d6;
  border-bottom: none;
}
.MuiButtonBase-root.MuiTab-root.MuiTab-textColorInherit {
  background: none;
  color: #bdc3c7;
  border: none;
  border-bottom: 2px solid #e8e2d6;
}
.MuiTabs-indicator {
  display: none;
}