@import './src/varsAndMixins.scss';
.nav-footer.print {
  display: none;
}
footer {
  @include flex-space-evenly;
  max-width: 1280px;
  margin: 0 auto;
  height: 450px;
}
// left bar ---------------------------------------------
.footer_contacts {
  padding: 25px 0 0 50px;
  min-width: 220px;
  p {
    color: black;
  }
}
.footer_contacts-links a {
  text-decoration: none;
  color: $footer-links-color;
  display: block;
  margin-top: 5px;
  // hover
  &:hover {
    color: $hover-text-color !important;
  }
}
// center ---------------------------------------------
.footer_menu-wrapper {
  @include flex-space-evenly;
    padding-top: 0;
    width: 70%;
}
.footer_menu-wrapper-mob {
  width: 70%;
  display: none;
}
.footer_menu-links {
  position: relative;
  margin-top: 60px;
  list-style: none;
  transition:  all ease .3s;
  a {
    font-size: 14px;
    color: $footer-links-color;
    text-decoration: none;
  }
  li {
    margin: 20px 0;
  }
  // hover
  li:hover {
    color: $hover-text-color;
  }
}
.footer_menu-links-title {
  white-space: nowrap;
  position: absolute;
  top: -50px;
  text-transform: uppercase;
  margin-bottom: 25px;
  font-size: 14px;
}
// right bar ---------------------------------------------
.footer_social {
  padding-top: 45px;
  min-width: 120px;
  padding-right: 50px;
  @include flex-space-evenly;
  svg {
    height: 20px;
    width: 20px;
  }
  svg:hover {
    transition: all .3s ease;
    transform: scale(1.2);
    fill: green;
  }
}


@media(max-width: 890px) {
  footer {
    height: 100%;
    @include flex-column-start;
  }
  .footer_contacts,.footer_social {
    width: 100%;
    padding: 0;
  }
  .footer_social {
    justify-content: center;
    height: 100px;
    a {
      margin: 0 15px;
    }
  }
  .footer_copyright,.footer_contacts-mob {
    margin-left: 25px;
  }
  .footer_contacts-mob {
    a,p {
      color: rgb(69, 65, 62);
    }
  }
  .footer_menu-wrapper,.footer_contacts-title,.footer_contacts-links,.footer_contacts-address {
    display: none;
  }
  .footer_menu-wrapper-mob {
    margin: 0 auto;
    width: 70%;
    display: block;
  }
  .footer_copyright {
    margin: 20px auto 0;
    padding-left: 50px;
    width: 70%;
  }
  .submenu_links-mob {
    li {
      color: black;
      &:hover {
        color: $hover-text-color;
      }
    }
  }
  .MuiExpansionPanel-root:before {
    background: none !important;
  }
}


@media(max-width: 600px) {
  footer {
    overflow: hidden;
  }
  .footer_copyright {
    width: 100%;
    padding-left: 25px;
  }
  .footer_menu-wrapper-mob {
    width: 100%;
  } 
}

