// color - select
.color-select-wr {
  margin-top: 40px;
  margin-bottom: 24px;
  display: inline-block;
  .color-select {
    width: 156px;
    outline: none;
    border: 2px solid #e8e2d6;
    border-radius: 40px;  
    .color-select-menu {
      border: 2px solid #e8e2d6;
      border-radius: 6px;
    }
    .color-select-MenuOuter {
      border-radius: 6px;
    }
    .color-select-option {
      text-align: left;
      padding-left: 40px;
      color: #45413e;
      font-family: 'Lato', sans-serif;
      font-size: 14px;
    }
    .color-select-control {
      border: none;
      border-radius: 40px; 
      padding-left: 40px; 
      padding-right: 10px;
    }
    .color-select-placeholder {
      text-align: left;
      background-image: url('/components/select_product/color-gray.png');
      background-size: 18px;
      background-position: 8%;
      background-repeat: no-repeat;
    }
    .color-select-MultiValueWrapper {
      outline: none;
    }
    --styled-select-placeholder__color: #45413e;
    --styled-select-placeholder__font-family: 'Lato', sans-serif;
    --styled-select-placeholder__font-size: 14px;
    --styled-select-placeholder__padding: 0 10px 0 40px;
  }
  .color-select-arrowZone {
    .color-select-arrow {
      border: none;
      background-image: url('/sort-down.png');
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      transform: rotate(0);
    }
  }
  .color-select-menu {

    [data-select-option] {
      background-position: 8%;
      background-repeat: no-repeat;
      background-size: 18px;
    }
    [data-select-option="gray"] {
      background-image: url('/components/select_product/color-gray.png');
    }
    [data-select-option="orange"] {
      background-image: url('/components/select_product/color-orange.png');
    }
    [data-select-option="blue"] {
      background-image: url('/components/select_product/color-blue.png');
    }
    [data-select-option="pink"] {
      background-image: url('/components/select_product/color-pink.png');
    }
    [data-select-option="azure"] {
      background-image: url('/components/select_product/color-azure.png');
    }
  }
}