@import './src/varsAndMixins.scss';
.product {
  max-width: 1280px;
  margin: 70px auto 0;
  min-height: 750px;
  background: #f1edea;
}
// popular items -----------------------------------------
.product__popular-items-title {
  @include flex-center-center;
  text-align: center;
  margin-top: 50px;
  h4  {
    margin: 10px -30px 0 -30px;
    padding: 0;
    font-size: 25px;
    color: #45413e;
    white-space: nowrap;
  }
  hr {
    width: 100%;
    padding: 0;
    margin: -15px 50px -25px 50px;
    color: #D3D3D3;
  }
}
// scroll ------------------------------------------------
.product__scroll-btn-wr {
  margin-bottom: 50px;
  display: none;
  .product__scroll-btn {
    color: $light-text-color;
    border: $border-color 2px solid;
    width: 150px;
    border-radius: 40px;
    background: $main-green-color;
    display: block;
    margin: 0 auto;
    // hover 
    &:hover {
      background: white;
      color: $main-green-color;
    }
  }
}

@media(max-width: 700px) {
  .product {
    padding-top: 20px;
  }
  .product__scroll-btn-wr {
    display: block;
  }
}
@media(max-width: 500px) {
  .product__popular-items-title {
    h4  {
      font-size: 15px;
      margin: 10px -30px 0 -30px;
    }
    hr {
      margin: -15px 50px -25px 50px;
    }
    hr:first-child {
      margin-left: 20px;
    }
    hr:last-child {
      margin-right: 20px;
    }
  }
}

