@import './src/varsAndMixins.scss';
.cookie {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0 auto;
  background-color: rgba(0,0,0,0.4);
  z-index: 999;
  display: none;
  &.isVisible {
    display: block;
  }
}
.cookie__content {
  @include display-flex-column-start;
  justify-content: center;
  position: relative;
  height: 150px;
  background: white;
  max-width: 1180px;
  margin: 100px auto;
  h6 {
    font-size: 16px;
    color: $main-green-color;
    margin: 0 0 0 20px;
  }
  p {
    margin-left: 20px;
    font-size: 14px;
    color: black;
    line-height: 1.7;
  }
  span a {
    color: $main-green-color;
    text-decoration: none;
    // hover
    &:hover {
      color: green;
    }
  }
}
.cookie__accept-btn {
  cursor: pointer;
  border: none;
  color: white;
  background: $main-green-color; 
  height: 40px;
  width: 140px;
  padding: 0;
  border-radius: 30px;
  position: absolute;
  top: 95px;
  right: 20px;
  border: 1px solid white;
  // hover
  &:hover {
    background: white;
    border: 1px solid $main-green-color;
    color: $main-green-color;
  }
}
.cookie__close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  border: none;
  background: none; 
  cursor: pointer;
  img {
    height: 25px;
    width: 25px;
  }
}

@media(max-width: 1500px) {
  .cookie__content {
    p {
      padding-right: 170px;
    }
  }
}
@media(max-width: 1280px) {
  .cookie__content {
    width: 93%;
  }
}
@media(max-width: 1100px) {
  .cookie__content {
    height: 200px;
  }
}
@media(max-width: 1000px) {
  .cookie__content {
    padding-top: 30px;
    height: 300px;
    p {
      padding-right: 20px;
    }
    .cookie__accept-btn {
      position: relative;
      top: 0;
      left: 0;
      margin: 10px auto;
    }
  }
}

