.print {
  max-width: 1280px;
  margin: 70px auto 0;
  .product__add-to-cart-btn,.product__share,.product_btns-wrapper,.image-gallery-fullscreen-button {
    display: none;
  }
  .product__links {
    a {
      color: black;
    }
    p {
      color: gainsboro;
    }
  }
  .image-gallery-thumbnail.active {
    border: 2px solid #a2a09e;
  }
  .color-select-wr {
    display: none;
  }
  .product__add-to-cart-wrap {
    display: none;
  }
  .print__color,.print__qty {
    display: block;
    font-weight: bold;
    margin-top: 40px;
  }
  .print__qty {

  }
}
