.product__popular-items-slider {
  padding-bottom: 30px;
  max-width: 1180px;
  margin: 0 auto;
  .slick-dots { 
    bottom: -35px;
  }
  .slick-dots li button:before {
      color: black;
      height: 10px;
      width: 10px;
      font-size: 10px;
  }
  .slick-dots li.slick-active button:before { 
    color: transparent;
    opacity: 1;
    border: 3px solid black;
    border-radius: 100%;
    height: 10px;
    width: 10px;
  }
}
@media(max-width:700px) {
  .product__popular-items-slider {
    padding-bottom: 70px;
  }
}