@import './src/varsAndMixins.scss';
/* progress */
.release {
  padding: 100px 50px 50px 50px;
  text-align: center;
  background: #f1f1f1;
  max-width: 1180px;
  margin: 0 auto;
}
.release__wrapper {
  max-width: 1140px;
  margin: 0 auto;
  background: white;
  min-height: 500px;
  padding: 50px 20px 20px 20px;
  h1 {
    font-size: 30px;
    color: rgb(69, 65, 62);
  }
  .progress-wrapper {
    @include flex-justify-center;
    margin-top: 70px;
  }
  .seconds-progress,.minutes-progress,.hours-progress,.days-progress,.months-progress {
    @include flex-center-center;
    position: relative;
    height: 110px;
    width: 110px;
    h2 {
      font-size: 45px;
    }
  }
  .seconds-svg,.minutes-svg,.hours-svg,.days-svg,.months-svg {
    position: absolute;
    top: calc(50% - 60px);
    left: calc(50% - 60px);
    transform: rotate(-90deg);
  }
  h3 {
    position: absolute;
    top: 100%;
  }
}
.release__inp-wr {
  @include flex-justify-start;
  height: 40px;
  max-width: 500px;
  margin: 100px auto 0;
  align-items: center;
  border: 2px solid $border-color;
  border-radius: 40px;
  position: relative;
  input {
    height: 30px;
    width: 430px;
    padding: 0 10px;
    margin-left: 15px;
    border: none;
    outline: none;
    &::placeholder {
      font-size: 13px;
    }
  }
  img {
    position: absolute;
    top: 7px;
    right: 10px;
    cursor: pointer;
  }
}
@media(max-width: 700px) {
  .release__wrapper {
    padding: 20px 20px 20px 20px;
    .progress-wrapper {
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      margin-top: 10px;
    }
    .seconds-progress,.minutes-progress,.hours-progress,.days-progress,.months-progress {
      margin-bottom: 30px;
    }
    h3 {
      top: 97px;
    }
  }
  .release__inp-wr {
    margin: 10px auto;
    input {
      width: 280px;
      &::placeholder {
        font-size: 11px;
      }
    }
  }
}
@media(max-width: 400px) {
  .release__inp-wr {
    input {
      width: 270px;
      &::placeholder {
        font-size: 8px;
      }
    }
  }
}