@import './src/varsAndMixins.scss';
.cart {
  max-width: 1180px;
  margin: 0 auto;
  background: #f1edea;
  min-height: 500px;
  padding: 0 50px 50px 50px;
}
// title --------------------------------------------------
.cart__links {
  @include flex-center-center;
  margin-top: 70px;
  a {
    text-decoration: none;
    color: green;
  }
  p {
    color: #45413e;
  }
  span {
    margin: 0 20px;
    color: #45413e;
  }
}
.cart__title {
  @include flex-center-center;
  text-align: center;
  h4  {
    margin: 10px -30px 30px -30px;
    padding: 0;
    font-size: 50px;
    color: #45413e;
    white-space: nowrap;
  }
  hr {
    width: 100%;
    padding: 0;
    margin: -15px 50px 0 50px;
    color: #D3D3D3;
    &:first-child {
      margin-left: 0;
    }
    &:last-child {
      margin-right: 0;
    }
  }
}
// cart list wrapper --------------------------------------
.cart__list-wrapper {
  max-width: 1080px;
  margin: 0 auto;
  background: white;
  padding-top: 100px;
  padding-left: 100px;
  padding-bottom: 30px;
}
.cart__item-wr {
  display: flex;
  margin-bottom: 30px;
}
.cart__item-left-bar {
  display: flex;
  width: 60%;
  min-width: 300px;
  img {
    max-height: 100px;
    max-width: 100px;
    margin-right: 20px;
  }
}
.cart__item-product-text {
  h4 {
    color: #83807e;
  }
  p {
    color: #cacfd2;
  }
}
.cart__item-right-bar {
  @include flex-space-between-align-center;
  width: 30%;
  height: 60px;
  min-width: 350px;
  margin: 0 30px;
  // del button 
  .cart__del-btn {
    cursor: pointer;
    height: 25px;
    width: 25px;
    padding: 0 0 6px 0;
    margin: 0;
    border-radius: 100%;
    border: 2px solid $border-color;
    background: none;
    color: $border-color;
  }
}
.cart__product-price {
  color: #45413e;
  font-size: 30px;
  font-weight: bold;
  white-space: nowrap;
  margin: 0 10px;
}
.cart__empty-list {
  text-align: center;
  font-size: 30px;
  margin: -60px 100px 0 0;
  padding: 0;
}
// button + -    ------------------------------------------
.cart__product-quantity {
  @include flex-center-center;
  width: 105px;
  border: $border-color 2px solid;
  border-radius: 40px;
  input {
    height: 30px;
    width: 40px;
    color: $dart-text-color;
    border: none;
    text-align: center;
    outline: none;
  }
  .cart__plus-btn, .cart__minus-btn {
    cursor: pointer;
    background: none;
    border: none;
    outline: none;
  }
}
// order --------------------------------------------------
.cart__order-wrapper {
  max-width: 1180px;
  margin: 0 auto;
  background: white;
  min-height: 450px;
  border-top: 2px solid #e8e2d6;
  display: flex;
  .cart__btn {
    color: $light-text-color;
    border: $main-green-color 2px solid;
    width: 150px;
    height: 40px;
    margin-left: 30px;
    border-radius: 40px;
    background: white;
    color: $main-green-color;
    // hover 
    &:hover {
      background: $main-green-color;
      color: white;
    }
    &.checkout {
      width: 210px;
      background: $main-green-color;
      color: white;
      &:hover {
        background: white;
        color: $main-green-color;
      }
    }
  }
}
// cart order left 
.cart__order-left {
  height: 100px;
  width: 40%;
  padding: 70px 0 0 100px;
  display: flex;
  input {
    border: 2px solid #e8e2d6;
    width: 180px;
    border-radius: 40px;
    height: 35px;
    padding: 0 20px;
  }
}
// cart order right 
.cart__order-right {
  height: 400px;
  width: 60%;
  padding: 70px 0 0 0;
  margin: 0 30px;
  p {
    font-size: 20px;
    text-transform: uppercase;
  }
  span {
    font-weight: bold;
  }
}
.cart__subtotal {
  display: flex;
  margin-left: 30px;
  p:last-child {
    margin-left: 145px;
  }
}
// cart shipping ------------------------------------------
.cart__shipping {
  display: flex;
}
// shipping left
.cart__shipping-left {
  margin-left: 30px;
  > P {
    margin: 0;
  }
}
// shipping right 
.cart__shipping-right {
  margin-left: 50px;
}
.cart__shipping-inp-wr {
  input {
    cursor: pointer;
    margin: 10px 10px 10px 0;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      top: -3px;
      left: -2.5px;
      height: 15px;
      width: 15px;
      background: white;
      border: 1px solid #e8e2d6;
      border-radius: 100%;
    }
    &:checked { 
      &::after {
        content: '';
        position: absolute;
        top: 0.5px;
        left: 1px;
        height: 10px;
        width: 10px;
        background: $main-green-color;
        border-radius: 100%;
      }
    }
  }
  label {
    color: #514e4b;
  }
  span {
    font-weight: 900;
    margin-left: 10px;
  }
}
.cart__order-total {
  @include flex-align-center;
  margin-left: 20px;
  p {
    margin: 30px 0 0 10px;
  }
  p:last-child {
    margin-left: 170px;
    font-size: 40px;
  }
}

@media(max-width: 1160px) {
  .cart__order-wrapper {
  }
  .cart__order-left {
    width: 30%;
    min-width: 400px;
    padding-left: 20px;
  }
}
@media(max-width: 1090px) {
  .cart__order-wrapper {
    @include flex-wrap-justify-center;
  }
  .cart__order-left {
    width: 100%;
    height: 70px;
    justify-content: center;
  }
  .cart__order-right {
    @include display-flex-column-center;
    width: 100%;
    padding-top: 0;
  }
  .cart__order-left input {
    width: 160px;
  }
  .cart__order-total p {
    width: 195px;
  }
  .cart__order-right p {
    width: 195px;
  } 
  .cart__subtotal p:last-child {
    margin-left: 0;
  }
  .cart__shipping-left {
    width: 180px;
  }
  .cart__order-total p:last-child {
    margin-left: 0;
    font-size: 30px;
  }
}
@media(max-width: 920px) {
  .cart__list-wrapper {
    padding-top: 30px;
    padding-left: 30px;
  }
  .cart__title h4  {
    font-size: 35px;
  }
  .cart__empty-list {
    margin: 0;
  }
}
@media(max-width: 850px) {
  .cart__item-wr {
    @include flex-wrap-justify-center;
  }
  .cart__item-left-bar {
    min-width: 430px;
  }
  .cart__item-right-bar {
    min-width: 430px;
  }
}
@media(max-width: 700px) {
  .cart {
    padding: 20px 20px 50px 20px;
  }
  .cart__links span {
    margin: 0 10px;
  }
  .cart__title h4  {
    font-size: 25px;
  }
}
@media(max-width: 600px) {
  .cart__list-wrapper {
    padding-left: 0;
  }
}
@media(max-width: 530px) {
  .cart__order-left {
    @include display-flex-column-center;
    min-width: 100px;
    padding: 10px 0 0 0;
    input {
      width: 120px;
      margin-bottom: 10px;
    }
  }
  .cart__order-right {
    height: 100%;
  }
  .cart__order-wrapper  { 
    .cart__btn {
      margin: 0;
    }
    .cart__btn.checkout {
      width: 150px;
      height: 60px;
    }
  }
  .cart__order-right-btns {
    @include display-flex-column-center;
    .cart__btn {
      margin: 10px 0 0 0;
    }
  }
  .cart__subtotal {
    flex-direction: column;
    p {
      margin: 10px 0 0 0;
    }
  }
  .cart__shipping {
    flex-direction: column;
  }
  .cart__shipping-left {
    margin-top: 10px;
  }
  .cart__shipping-right {
    margin: 10px 0 0 20px;
  }
  .cart__order-total {
    flex-direction: column;
    padding: 10px 0;
    p {
      margin: 0;
      padding: 0;
    }
    p:last-child {
      margin-top: 5px;
      font-size: 25px;
    }
  }
}
@media(max-width: 520px) {
  .cart__item-left-bar {
    min-width: 330px;
  }
  .cart__item-right-bar {
    min-width: 330px;
  }
  .cart__product-price {
    font-size: 20px;
  }
}
@media(max-width: 420px) {
  .cart__item-left-bar {
    min-width: 270px;
  }
  .cart__item-right-bar {
    min-width: 270px;
  }
}
@media(max-width: 350px) {
  .cart__item-left-bar {
    min-width: 250px;
  }
  .cart__item-right-bar {
    min-width: 250px;
  }
  .cart__product-price {
    font-size: 15px;
  }
}


