@import "~slick-carousel/slick/slick.css"; 
@import "~slick-carousel/slick/slick-theme.css";
@import './src/varsAndMixins.scss';
.slider-wrapper {
  @include display-flex-row-justify-between;
  height: 630px;
}
.gallery {
  padding-top: 90px;
  max-width: 880px;
  width: 100%;
  // slider styles ---------------------------------
  .slick-slider {
    margin-left: 50px;
  }
  .slide {
    position: relative;
  }
  .slick-dots {
    bottom: 50px;
  }
  .slick-dots li button:before {
      color: white;
      height: 10px;
      width: 10px;
      font-size: 10px;
  }
  .slick-dots li.slick-active button:before { 
    color: transparent;
    opacity: 1;
    border: 3px solid white;
    border-radius: 100%;
    height: 10px;
    width: 10px;
  }
}

.slide-content {
  position: absolute;
  left: 0;
  right: 0;
  h2, p {
    color: $light-text-color;
  }
  h2 {
    font-size: 50px;
    margin: 30px 50px;
  }
  p {
    font-size: 14px;
    margin: 50px 50px 10px;
  }
  .btn-view-more {
    color: white;
    border: white 1px solid;
    width: 150px;
    margin: 20px 50px;
    border-radius: 40px;
    &:hover {
      background: $hover-text-color;
    }
  }
}
.slide-content.alignment-left {
  @include display-flex-column-start;
  text-align: left;
}
.slide-content.alignment-center {
  text-align: center;
  @include display-flex-column-center;
}
.slide-content.alignment-right {
  @include display-flex-column-end;
  text-align: right;
}
// right bar -----------------------------------
.sales {
  margin-right: 50px;
  padding-top: 0;
}
.sales_content-wrapper {
  position: relative;
  background: #84bc22;
  width: 270px;
  height: 335px;
  margin-top: -10px;
  img {
    position: absolute;
    bottom: 0;
    left: 0;
  }
  h3 {
    padding-top: 60px;
    margin-bottom: 30px;
    font-size: 100px;
    font-weight: 900;
    color: $light-text-color;
  }
  p {
    font-size: 25px;
    color: $light-text-color;
    text-transform: uppercase;
  }
  h5 {
    margin: 100px 0 20px 0;
    padding: 0;
    font-size: 15px;
    color: rgb(69, 65, 62);
  }
  h6 {
    margin: 0;
    padding: 0;
    font-size: 14px;
    color: rgb(69, 65, 62);
  }
  .btn-follow {
    color: #3a5b96;
    border: #3a5b96 1px solid;
    height: 40px;
    width: 150px;
    margin: 27px 50px 0;
    border-radius: 40px;
  }
  // hover
  .btn-follow:hover {
    background: #3a5b96;
    color: white;
    .facebook {
      fill: white;
    }
  }
  .facebook {
    height: 20px;
    margin: 0 10px 3px 0;
  }
}

@media(max-width: 1220px) {
  .gallery {
    .slick-slider {
      width: 750px;
    }
  }
}
@media(max-width: 1170px) {
  .gallery {
    .slick-slider {
      width: 700px;
    }
  }
}
@media(max-width: 1100px) {
  .gallery {
    .slick-slider {
      width: 650px;
    }
  }
  .slide-content {
    h2 {
      font-size: 40px;
      margin: 40px 50px;
    }
  }
}
@media(max-width: 1040px) {
  .sales {
    display: none;
  }
  .gallery { 
    max-width: 100%;
    overflow: hidden;
    .slick-slider {
      width: 100%;
      margin: 0;
      padding: 0 50px;
    }
  }
}
@media(max-width: 750px) {
  .gallery { 
    .slider-wrapper {
      height: 100%;
    }
    .slick-slider {
      padding: 0 20px;
    }
    .slick-dots {
      position: relative;
    }
  }
  .slide-content {
    h2 {
      font-size: 30px;
      margin: 40px 50px;
    }
    p {
      font-size: 15px;
    }
  }
} 
@media(max-width: 600px) {  
  .gallery { 
    .slick-dots {
      margin-top: 5px;
    }
  }
  .slide-content {
    h2 {
      margin: 40px 20px;
    }
    p {
      margin: 50px 20px 10px;
    }
    a { 
      margin: 100px auto;
    }
  }
}
@media(max-width: 440px) { 
  .slide-content {
    h2 {
      margin: 30px 20px;
    }
    p {
      margin: 20px 20px 10px;
    }
    a { 
      margin: 30px auto;
    }
  }
}