$primary-background-color: #262a32;
$links-color: black;
$submenu-links-color: white;
$footer-links-color: black;
$hover-text-color: rgb(132, 188, 34);
$light-text-color: white;
$dart-text-color: black;
$main-green-color: #84bc22;
$hr-color: #3c4047;
$border-color: #e8e2d6;
// flex
@mixin flex-center-center {
  display: flex;
  justify-content: center;
  align-items: center;
}
@mixin flex-center-center-column {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
@mixin flex-space-evenly-center {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
@mixin flex-space-around-center {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
@mixin flex-space-evenly {
  display: flex;
  justify-content: space-evenly;
}
@mixin flex-justify-center {
  display: flex;
  justify-content: center;
}
@mixin flex-justify-start {
  display: flex;
  justify-content: flex-start;
}
@mixin flex-align-center {
  display: flex;
  align-items: center;
}
@mixin flex-space-between {
  display: flex;
  justify-content: space-between;
}
@mixin flex-space-between-align-center {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@mixin flex-column-center {
  flex-direction: column;
  justify-content: center;
}
@mixin flex-column-start {
  flex-direction: column;
  justify-content: flex-start;
}
@mixin flex-wrap-justify-center {
  flex-wrap: wrap;
  justify-content: center;
}
@mixin display-flex-column {
  display: flex;
  flex-direction: column;
}
@mixin display-flex-column-start {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
@mixin display-flex-column-center {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@mixin display-flex-column-justify-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@mixin display-flex-row-justify-between {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@mixin display-flex-column-end {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
// visibility
@mixin hidden {
  visibility: hidden;
  opacity: 0;
}
@mixin visible {
  visibility: visible;
  opacity: 1;
}
