@import './src/varsAndMixins.scss';
.nav-header {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      z-index: 999;
      background: white;
      &.print {
            display: none;
      }
}
nav {
      @include flex-space-around-center;
      height: 70px;
      box-sizing: border-box;
      margin: 0 auto;
      border-bottom: 2px solid rgb(232, 226, 214);
      position: relative;
}
.logo {
      @include flex-center-center;
      width: 147px;
      height: 65px;
      margin-left: 20px;
}
.logo_inner {
      @include flex-center-center;
}
.logo_text {
      p {
            &:first-child {
                  color: #011847;
                  font-size: 32px;
                  font-weight: 900;
                  text-transform: uppercase;
                  letter-spacing: 6.33px;
                  line-height: 0;
                  padding: 0;
                  margin: 12px 0;
            }
            &:last-child {
                  color: #84bc22;
                  font-size: 18px;
                  font-weight: 400;
                  padding: 0;
                  margin: 0;
            }
      }
}
.logo_img {
      width: 63px;
      height: 65px;
      margin-right: 10px;
}
// hamburger btn
.MuiIconButton-colorInherit.MuiIconButton-edgeStart {
      display: none;
}
.menu {
      display: flex;
}
.arrow {
      position: absolute;
      top: 30px;
      left: 85%;
      height: 10px;
}
.menu_links {
      position: relative;
      margin: 0 0;
      display: flex;
      text-transform: uppercase;
      line-height: 1.2;
      color: $links-color;
      padding: 27px 25px 25px;
      border: 2px solid white;
      border-bottom: 2px solid rgb(232, 226, 214);
      transition:  all ease .3s;
      // hover
      &:hover {
            color: $hover-text-color;
      }
} 
// hover
.menu_links:hover {
      color: $submenu-links-color;
      background: $primary-background-color;
      border: 2px solid rgb(232, 226, 214);
      border-bottom: 2px solid rgb(132, 188, 34);
      .submenu-wrapper {
            @include visible;
      }
} 
.submenu-wrapper {
      @include flex-space-evenly-center;
      @include hidden;
      background: $primary-background-color;
      transition:  all ease .3s;
      width: 1280px;
      height: 450px;
      box-sizing: border-box;
      position: absolute;
      top: 69px;
      left: -307px;
      text-transform: uppercase;
}
.submenu_links {
      position: relative;
      padding: 0;
      li {
            color: $submenu-links-color;
            text-transform: none;
            margin: 15px 0;
      }
      // hover 
      li:hover {
            color: $hover-text-color;
      }
}
.submenu_links-title {
      color: $submenu-links-color;
      white-space: nowrap;
      text-transform: uppercase;
      position: absolute;
      top: -70px;
      left: 0;
}
.submenu_links-mob {
      li {
            color: $submenu-links-color;
      }
}
.selects {
      @include flex-space-between;
      width: 120px;
}
.selects_select-wr {
      position: relative;
      &::after {
            position: absolute;
            content: '';
            top: 7px;
            right: -9px;
            height: 10px;
            width: 10px;
            background-image: url('/sort-down.png');
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
      }
      select {
            font-size: 14px;
            color: #a8a6a5;
            border: none;
            -webkit-appearance: none;
            -moz-appearance: none;
            appearance: none;
            outline: none;
            cursor: pointer;
      }
}
.nav_icons-wrapper {
      @include flex-space-between;
      width: 170px;
      margin-right: 30px;
}
.nav_icons-img-wr {
      cursor: pointer;
      position: relative;
      img {
            display: block;
      }
      &:hover {
            &::before {
                  content: '';
                  position: absolute;
                  top: -8px;
                  left: -7px;
                  height: 40px;
                  width: 40px;
                  border-radius: 100%;
                  background: rgba(70, 64, 64, 0.3);     
            }
      }
}
// mobile
.nav_icons-img-wr.mob {
      &:hover {
            &::before {
                  content: '';
                  background: rgba(207, 198, 198, 0.3);     
            }
      }
}
.nav_icons-wrapper-mob {
      @include hidden;
      display: none;
      transition: .3 all ease;
      &.active {
            @include visible;
      }
      img {
            cursor: pointer;
      }
}
.mobile-menu {
      @include hidden;
      display: none;
      transition: .3s all ease;
      &.active {
            @include visible;
      }
}
// ExpansionPanel img arrow
.MuiIconButton-root.MuiExpansionPanelSummary-expandIcon {
      img {
            height: 12px;
            width: 12px;
      }
}
@media(max-width: 1200px) {
      .nav_icons-wrapper {
            width: 170px;
            margin: 0 12px;
      }
      .menu {
            margin-left: -50px;
      }
}
@media(max-width: 1150px) {
      .submenu-img {
            display: none;
      }
}
@media(max-width: 1050px) {
      .menu_links {
            padding: 27px 20px 25px;
      }
      .submenu_links {
            margin: 0 -150px;
      }
      .logo_text {
            p:first-child {
                  font-size: 26px;
            }
            p:last-child {
                  font-size: 15px;
            }
      }
      .selects {
            width: 100px;
      }
      .selects_select-wr {
            &::after { 
                  top: 7px;
                  right: -15px;
            }
      }
      .nav_icons-wrapper { 
            width: 140px;
            img {
                  height: 22px;
                  width: 22px;
            }
      }
}
@media(max-width: 890px) {
      nav {
            max-width: 100%;
      }
      .logo_text {
            display: none;
      }
      .menu {
            display: none;
      }
      // hamburger btn
      .MuiIconButton-colorInherit.MuiIconButton-edgeStart {
            display: block;
            position: absolute;
            top: 10px;
            left: 30px;
            &.menu-btn.active {
                  display: none;
            }
            &.close-btn {
                  display: none;
            }
            &.close-btn.active {
                  display: block;
            }
      }
      .mobile-menu  {
            display: block;
            position: absolute;
            top: 100%;
            width: 100%;
            background: $primary-background-color;
            a li {
                  color: $submenu-links-color;
            }
            li:hover {
                  color: $hover-text-color;
            }
            .submenu-img-mob {
                  display: block;
                  margin: 0 auto;
                  padding: 30px 0;
            }
      }
      .nav_icons-wrapper {
            display: none;
      }
      .nav_icons-wrapper-mob {
            @include flex-space-evenly-center;
            position: fixed;
            bottom: 0;
            left: 0;
            right: 0;
            max-width: 100%;
            height: 55px;
            margin: 0 auto;
            background: $primary-background-color;
      }
      .selects {
            display: none;
      }
}
@media(max-width: 700px) {
      .nav-header {
            position: absolute;
            top: 0;
            left: 0;
      }
      .mobile-menu  {
            .submenu-img-mob {
                  padding: 15px 0;
            }
      }
}




