@import './src/varsAndMixins.scss';
.login,.login__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 990;
  @include hidden;
  &.loginIsVisible {
    @include visible;
    .login__overlay {
      @include visible;
    }
    .login__content {
      transform: scale(1);
    }
  }
}
.login__overlay {
  cursor: pointer;
  background-color: rgba(0,0,0,0.8);
  z-index: 990;
  @include hidden;
}
// form -------------------------------------------
form {
  @include display-flex-column;
}
.login__password-wrapper {
  position: relative;
  height: 70px;
}
.login__show-password-btn {
  cursor: pointer;
  height: 35px;
  position: absolute;
  top: 0;
  right: 85px;
  background: none;
  border: none;
  img {
    display: block;
    height: 20px;
    width: 30px;
    margin-top: 0;
  }
}
.login__email,.login__password {
  height: 30px;
  width: 300px;
  padding: 0 20px;
  margin: 0 auto;
  border-radius: 30px;
  border: 2px solid #d7d7d7;
}
.login__forgot-password {
  position: absolute;
  top: -27px;
  right: 100px;
}
label {
  width: 300px;
  margin: 10px auto;
  text-align: left;
}

// content ----------------------------------------
.login__content {
  @include display-flex-column-justify-center;
  transform: scale(0);
  transition: all ease .5s;
  position: relative;
  height: 500px;
  background: white;
  width: 500px;
  max-width: 1280px;
  margin: 100px auto 0;
  text-align: center;
  z-index: 991;
  border-radius: 6px;
  h6 {
    font-size: 30px;
    margin: 0 0 30px 0;
  }
  a {
    text-decoration: none;
    color: $main-green-color;
  }
}
// btns -------------------------------------------
.login__accept-btn {
  cursor: pointer;
  border: none;
  color: white;
  background: $main-green-color; 
  height: 40px;
  width: 200px;
  padding: 0;
  margin: 20px auto;
  border-radius: 30px;
  border: 1px solid white;
  &:hover {
    background: white;
    color: $main-green-color;
    border: 1px solid $main-green-color;
  }
}
.login__close-btn {
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
  border: none;
  background: none;
  img {
    height: 20px;
    width: 20px;
  }
}


@media(max-width: 600px) {
  .login__content {
    width: 400px;
  }
  .login__email,.login__password {
    width: 250px;
  }
  label {
    width: 280px;
  }
  .login__forgot-password {
    position: absolute;
    right: 60px;
  }
  .login__show-password-btn {
    right: 60px;
  }
}

@media(max-width: 400px) {
  .login__content {
    height: 400px;
    width: 300px;
  }
  .login__email,.login__password {
    width: 200px;
  }
  label {
    width: 230px;
  }
  .login__forgot-password {
    position: absolute;
    right: 35px;
  }
  .login__show-password-btn {
    right: 35px;
  }
}