@import './src/varsAndMixins.scss';
.category {
  max-width: 1280px;
  margin: 0 auto;
  text-align: center;
  background: #f2edea;
  padding: 100px 0;
}
.header-wr {
  max-width: 1180px;
  margin: 0 auto;
  padding: 0 50px;
}
// title --------------------------------------------------
.category__links {
  @include flex-center-center;
  a {
    text-decoration: none;
    color: green;
  }
  p {
    color: #45413e;
  }
  span {
    margin: 0 20px;
    color: #45413e;
  }
}
.category__title {
  @include flex-center-center;
  text-align: center;
  h4  {
    margin: 10px -30px 30px -30px;
    padding: 0;
    font-size: 50px;
    color: #45413e;
    white-space: nowrap;
  }
  hr {
    width: 100%;
    padding: 0;
    margin: -15px 50px 0 50px;
    color: #D3D3D3;
    margin: -15px 50px 0 50px;
    &:first-child {
      margin-left: 0;
    }
    &:last-child {
      margin-right: 0;
    }
  }
}
// toggle filter ------------------------------------------
.category__filter-visibility {
  cursor: pointer;
  margin: 0 0 20px;
  padding: 0;
  background: none;
  border: none;
  color: $main-green-color;
  display: none;
}
// selects ------------------------------------------------
.category__selects-main-wrapper {
  // max-width: 1180px;
  background: white;
  margin: 0 auto;
  &.ishidden {
    display: none;
  }
}
.category__selects {
  @include flex-space-between;
  padding: 30px;
}
.category__select-content-wr {
  @include flex-space-between-align-center;
  height: 40px;
  width: 250px;
  margin-top: 20px;
}
.category__select-wr {
  position: relative;
  margin: 0 10px;
  img {
    position: absolute;
    top: 13px;
    right: 12px;
    height: 10px;
    width: 10px;
  }
}
.category__select {
  height: 35px;
  width: 170px;
  border: 2px solid #a2a09e;
  border-radius: 20px;
  padding-left: 10px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; 
}
// cards --------------------------------------------------
.categoty__cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding: 30px 28px 0;
}
// btn load more ------------------------------------------
.categoty__btn-load-more-wr {
  width: 100%;
  margin: 80px 0 70px 0; 
  .categoty__btn-load-more {
    color: white;
    border: white 1px solid;
    width: 160px;
    margin: 20px auto;
    border-radius: 40px;
    background: $main-green-color;
    border: 1px solid #f2edea;
    // hover 
    &:hover {
      background: #f2edea;
      color: $main-green-color;
      border: 1px solid $main-green-color;
    }
  }
}
@media(max-width: 1279px) {
  .category__title hr {
    &:first-child {
      margin-left: 0;
    }
    &:last-child {
      margin-right: 0;
    }
  }
}
@media(max-width: 900px) {
  .header-wr {
    padding: 0 20px;
  }
  .categoty__btn-load-more-wr {
    margin: 0;
  }
}
@media(max-width: 810px) {
  .category__select-content-wr {
    margin: 5px 100px;
  }
  .category__selects {
    flex-wrap: wrap;
    justify-content: center;
  }
  .category__filter-visibility {
    display: inline-block;
  }
}