@import './src/varsAndMixins.scss';
.featured-products {
  height: 560px;
  background: $primary-background-color;
  padding: 30px 0;
  h2 {
    margin: 0;
    padding: 10px 0 0;
    font-size: 30px;
    color: $light-text-color;
  }
  > p {
    color: $light-text-color;
    font-size: 16px;
    margin-bottom: 50px;
  }
}
.featured-products__slider-wrap {
  max-width: 1210px;
  margin: 0 auto;
  .slick-list { 
    border-radius: 6px; 
  }
  .slick-slider {
    &::before {
      content: '';
      position: absolute;
      height: 3px;
      width: 420px;
      background-color: #ffffff;
      left: calc(50% - 210px);
      top: -10px;
    }
    &::after {
      content: '///';
      position: absolute;
      background-color: $primary-background-color;
      padding: 2px 11px;
      font-style: italic;
      font-family: 'Lato', sans-serif;
      color: #ffffff;
      font-weight: 900;
      letter-spacing: 2px;
      font-size: 23px;
      left: calc(50% - 26px);
      top: -26px;
    }
  }
  .slick-prev, .slick-next {
    background-repeat: no-repeat;
    background-position: center;
    width: 30px;
    height: 30px;
    top: -9px;
    &::before { display: none; }
  }
  .slick-prev {
    left: calc(50% - 250px);
    background-image: url('/components/featured_products/arrow_slider_left.png');
  }
  .slick-next {
    left: calc(50% + 220px);
    background-image: url('/components/featured_products/arrow_slider_right.png');
  }
}


@media(max-width: 760px) {
  .featured-products {
    display: none;
  }
}