@import './src/varsAndMixins.scss';
.popular-items {
  min-height: 600px;
  background: $primary-background-color;
  margin: 0;
  padding: 30px 0;
}
.popular-items__title {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 50px;
  padding-bottom: 30px;
  h4 {
    color: $light-text-color;
    margin: 30px 20px 0 20px;
    padding: 0;
    font-size: 20px;
    white-space: nowrap;
  }
  hr {
    width: 100%;
    padding: 0;
    margin-top: 40px;
    border: 1px solid $hr-color;
  }
}
.popular-items__content {
  margin: 0 40px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.popular-items__content-mobile {
  display: none;
  min-height: 300px;
}
// buy btn ---------------------------------------
.popular-items__buy-button-wr {
  height: 30px;
  width: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
  border: 1px solid #eaeaea;
  overflow: hidden;
  margin: 3px auto 0;
}
.popular-items__buy-button-price {
  color: rgb(36, 195, 108);
}
.popular-items__buy-button {
  cursor: pointer;
  position: relative;
  right: -35px;
  border: none;
  background: none; 
  height: 100%;
  width: 100px;
  margin: 0;
  padding: 0;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  border-left: 1px solid #eaeaea;
  // hover
  &:hover {
    background: $hover-text-color;
  }
}
// btn load more ---------------------------------
.popular-items__btn-load-more-wr {
  width: 100%;
  .popular-items__btn-load-more {
    color: white;
    border: white 1px solid;
    width: 160px;
    margin: 20px auto;
    border-radius: 40px;
    img {
      margin-left: 10px;
    }
    // hover 
    &:hover {
      background: $hover-text-color;
    }
  }
}
.popular-items__card {
  position: relative;
  // hover
  &.simple:hover { 
    .popular-items__card-overlay { 
      visibility: visible;
      opacity: 0.905;
    }
  }
}
.popular-items__card-overlay {
  @include flex-center-center;
  @include hidden;
  position: absolute;
  top: 0;
  left: 0;
  height: 370px;
  width: 270px;
  background-image: linear-gradient(90deg, rgb(28,193,103) 0%, rgb(46,211,162) 100%);
  box-sizing: border-box;
  transition: all .3s ease;
  img {
    height: 90px;
    width: 90px;
  }
}
.popular-items__card-overlay.message {
  background-image: linear-gradient(90deg, rgb(103,91,153) 0%, rgb(220,89,135) 100%);
  visibility: visible;
  opacity: 0.905;
}

// media ------------------------------------------
@media (max-width: 1239px) {
  .popular-items__content {
    margin: 0 auto;
    max-width: 1030px;
    justify-content: center;
  }
  .popular-items__card {
    margin: 30px 35px !important;
  } 
}
@media (max-width: 950px) {
  .popular-items__content {
    justify-content: space-evenly;
  }
}
@media (max-width: 750px) {
  .popular-items__content {
    margin: 0 10px;
  }
  .popular-items__title {
    margin: 0 20px;
  }
}
@media (max-width: 699px) {
  .popular-items {
    min-height: 300px;
    padding: 50px 0;
  }
  .popular-items__content {
    display: none;
  }
  .popular-items__content-mobile {
    display: block;
    min-height: 300px;
    img {
      display: inline-block;
    }
    .slick-dots { 
      bottom: -35px;
    }
    .slick-dots li button:before {
        color: white;
        height: 10px;
        width: 10px;
        font-size: 10px;
    }
    .slick-dots li.slick-active button:before { 
      color: transparent;
      opacity: 1;
      border: 3px solid white;
      border-radius: 100%;
      height: 10px;
      width: 10px;
    }
  }
  .popular-items__card {
    margin: 0 auto !important;
  } 
}
