@import './src/varsAndMixins.scss';
.banner {
  @include flex-center-center-column;
  height: 450px;
  background-image: url('%PUBLIC_URL%/components/banner/banner.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  h2, p {
    color: $light-text-color;
  }
  h2 {
    margin: 0;
    padding: 30px 0 10px;
    font-size: 60px; 
  }
  p {
    font-size: 16px;
  }
}
@media (max-width: 600px) {
  .banner {
    h2 {
      font-size: 40px; 
    }
  }
}
@media (max-width: 400px) {
  .banner {
    h2 {
      padding: 100px 0 10px;
      font-size: 30px; 
    }
    p {
      font-size: 14px;
    }
  }
}