@import './src/varsAndMixins.scss';
@import "~react-image-gallery/styles/scss/image-gallery.scss";
.product__links {
  @include flex-center-center;
  a {
    text-decoration: none;
    color: green;
  }
  p {
    color: #45413e;
  }
  span {
    margin: 0 20px;
    color: #45413e;
  }
}
.product__title {
  @include flex-center-center;
  text-align: center;
  h4  {
    margin: 10px -30px 30px -30px;
    padding: 0;
    font-size: 50px;
    color: #45413e;
    white-space: nowrap;
  }
  hr {
    width: 25%;
    padding: 0;
    margin: -15px 50px 0 50px;
    color: #D3D3D3;
  }
}
// slider section ----------------------------------------
.product__slider-wrapper {
  @include flex-space-between;
  padding: 20px 30px 40px 20px;
  margin: 0 50px;
  background: white;
}
// left bar ----------------------------------------------
.product__slider-content-left {
  min-height: 550px;
  width: 60%;
}
// right bar ---------------------------------------------
.product__slider-content-right {
  width: 35%;
  min-height: 550px;
  width: 350px;
  margin-left: 30px;
}
.product__add-to-cart-wrap {
  @include flex-align-center;
  height: 40px;
  .product__add-to-cart-btn {
    color: $light-text-color;
    border: $border-color 2px solid;
    width: 150px;
    margin-left: 30px;
    border-radius: 40px;
    background: $main-green-color;
    // hover 
    &:hover {
      background: white;
      color: $main-green-color;
    }
  }
}
.product__add-to-cart-inp-wr {
  @include flex-center-center;
  width: 105px;
  border: $border-color 2px solid;
  border-radius: 40px;
  input {
    height: 30px;
    width: 40px;
    color: $dart-text-color;
    border: none;
    text-align: center;
    outline: none;
  }
  .product__plus-btn, .product__minus-btn {
    cursor: pointer;
    background: none;
    border: none;
  }
}
.print__color,.print__qty {
  display: none;
}
.product__price {
  color: #45413e;
  font-size: 48px;
  margin: 10px 0;
  padding: 0;
}
.product__text-wrapper {
  height: 95px;
  overflow: hidden;
  transition: all ease .3s;
  &.showMore {
    height: 450px;
  }
  p {
    font-size: 16px;
    color: rgb(69, 65, 62);
    margin-right: 20px;
  }
}
.product_btns-wrapper {
  display: flex;
  justify-content: space-between;
  a {
    color: $main-green-color;
    margin-right: 40px;
  }
}
// read more 
.product__readMore {
  cursor: pointer;
  margin: 0;
  padding: 0;
  background: none;
  border: none;
  color: $main-green-color;
}
.product__share {
  display: flex;
  align-items: center;
  margin-top: 20px;
  h6 {
    margin: 0 20px 0 0;
    padding: 0;
    color: #c7c6c5;
  }
}
.product_social {
  min-width: 150px;
  @include flex-space-between;
  svg {
    height: 20px;
    width: 20px;
  }
  svg:hover {
    transition: all .3s ease;
    transform: scale(1.2);
    fill: green;
  }
}
// slider styles ----------------------------------------
.image-gallery-fullscreen-button {
  top: 10px;
  left: 10px;
  height: 20px;
  width: 20px;
}
.image-gallery-thumbnail:first-child {
  margin-top: 0;
}
.image-gallery-thumbnails {
  margin-top: 10px;
  margin-left: 30px;
}
.image-gallery-thumbnail {
  border: 2px solid #a2a09e;
  margin-top: 20px;
}
.image-gallery-thumbnail.active {
  border: 2px solid $main-green-color;
}
.image-gallery-fullscreen-button {
  cursor: pointer;
  background-image: url('/pages/product/fullscreen.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.image-gallery-fullscreen-button::before {
  display: none;
}
.image-gallery-bullets {
  display: none;
}

@media(max-width: 1000px) {
  .product__links span {
    margin: 0 5px;
  }
  .product__title {
    h4  {
      font-size: 30px;
    }
    hr {
      margin: -15px 40px 0 40px;
    }
  }
  .product__tabs-wrapper {
    display: none;
  }
  .product__slider-wrapper {
    .image-gallery-slide-wrapper {
      min-height: 450px;
      min-width: 450px;
    }
    .image-gallery-content {
      @include display-flex-column;
    }
    .image-gallery-thumbnails-wrapper {
      width: 400px;
      height: 150px !important;
    }
    .image-gallery-thumbnails-container {
      @include flex-align-center;
      align-items: center;
    }
    .image-gallery-thumbnails-wrapper {
      width: 500px;
      margin: 0;
    }
    .image-gallery-thumbnails {
      width: 500px;
      margin: 10px 0 0 0;
    }
    .image-gallery-thumbnail {
      margin-top: 0;
    }
    .image-gallery-bullets {
      display: block;
      bottom: -150px;
    }
    .image-gallery-bullet {
      border: none;
      background: gray;
      box-shadow: none;
    }
    .image-gallery-bullets .image-gallery-bullet.active {
      background: white ;
      border: 2px solid black;
    }
  }
}
@media(max-width: 930px) {
  .product__slider-wrapper {
    flex-wrap: wrap;
    justify-content: center;
  }
  .product__slider-content-right {
    margin-top: 20px;
    min-height: 350px;
  }
}
@media(max-width: 800px) {
  .product__slider-wrapper {
    padding: 20px 0;
    margin: 0 20px;
    .product__slider-content-left {
      width: 100%;
    }
    .image-gallery-slide-wrapper {
      min-height: 270px;
      min-width: 270px;
    }
    .image-gallery-content { 
      align-items: center;
    }
    .image-gallery-thumbnails-container {
      justify-content: center;
    }
    .image-gallery-bullets .image-gallery-bullet {
      margin: 0 7px;
      padding: 7px;
    }  
  }
}
@media(max-width: 750px) {
  .product__slider-wrapper {
    margin: 0;
  }
}
@media(max-width: 700px) {
  .product__links {
    flex-wrap: wrap;
  }
  .product__title {
    h4  {
      font-size: 20px;
    }
    hr {
      margin: -15px 40px 0 40px;
    }
  }
}
@media(max-width: 600px) {
  .product__slider-wrapper {
    .image-gallery-thumbnails-wrapper {
      overflow: hidden;
      width: 335px;
    }
  }
}
@media(max-width: 500px) {
  .product__links {
    p {
      margin: 5px 0;
      padding: 0;
    }
  }
  .product__title h4 {
    white-space: pre-wrap;
  }
  .product__slider-content-left {
    min-height: 300px;
  }
  .product__slider-wrapper {
    .product__add-to-cart-wrap {
      height: 90px;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;
    }
    .product__add-to-cart-btn {
      margin: 0;
    }
  }
  .product__price {
    font-size: 30px;
  }
}
@media(max-width: 400px) {
  .product__slider-wrapper {
    .image-gallery-thumbnails-wrapper {
      overflow: hidden;
      width: 300px;
    }
    .image-gallery-thumbnail:last-child {
      display: none !important;
    }
  }
  .product__text-wrapper {
    &.showMore {
      height: 500px;
    }
  }
}
@media(max-width: 340px) {
  .product__text-wrapper {
    &.showMore {
      height: 580px;
    }
  }
}

