@import './src/varsAndMixins.scss';
.ServicesHomePage {
  max-width: 1280px;
  background: #45413e;
  height: 200px;
  padding: 20px 50px;
  display: flex;
}
.ServicesHomePage__content-wrapper {
  @include flex-space-between;
  width: 100%;
  max-width: 1180px;
  margin: 0 auto;
  text-align: left;
}
.ServicesHomePage__content {
  @include flex-center-center;
  height: 100%;
}
.ServicesHomePage__image {
  margin-right: 50px;
  margin-bottom: 15px;
}
.ServicesHomePage__text {
  h3 {
    margin: 0 0 10px 0;
    padding: 0;
    color: white;
  }
  p {
    margin: 0;
    padding: 0;
    color: grey;
  }
}

@media(max-width: 1150px) {
  .ServicesHomePage__content {
    padding: 0 10px;
  }
}
@media(max-width: 830px) {
  .ServicesHomePage {
    flex-direction: column;
    height: 100%;
  }
  .ServicesHomePage__content-wrapper {
    flex-wrap: wrap;
  }
  .ServicesHomePage__image {
    margin: 0 10px;
    padding: 0;
  }
  .ServicesHomePage__content {
    width: 400px;
    margin: 30px auto;
    justify-content: space-between;
    &.serv-center {
      flex-direction: row-reverse;
    }
  }
}